<script>
import gsap from "gsap";
export default {
  mounted() {
    const { top, right, bottom, left, mask, content } = this.$refs;

    const tl = gsap.timeline({ repeat: 0 });

    tl
      .to(top, 0.4, { width: "100%" })
      .to(right, 0.4, { height: "100%" })
      .to(bottom, 0.4, { width: "100%" })
      .to(left, 0.4, { height: "100%" })
      .to(mask, 0.7, { left: "100%" })
      .to(content, 0.6, { opacity: 1 });
  }
};
</script>
